html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
  background-color: #14213d;
}
body::-webkit-scrollbar {
  width: 0.7rem;
}
body::-webkit-scrollbar-thumb {
  background-color: #666666;
  border-radius: 2rem;
}

p {
  color: #ffffff;
  font-size: 1.5rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: white;
}

img, picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 2.5rem 0;
  color: #ffffff;
}

h1 {
  font-size: 2.5rem;
  font-weight: 900;
}

h2 {
  font-size: 2.3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.6rem;
}

.boton-amarillo {
  background-color: #fca311;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 2rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.boton-amarillo:hover {
  background-color: #f09703;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #fca311;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 2rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
}
.boton-amarillo-block:hover {
  background-color: #f09703;
  cursor: pointer;
}

.boton-bloqueado {
  background-color: #666666;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 2rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
  pointer-events: none;
}

.boton-bloqueado-block {
  background-color: #666666;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 2rem 6rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
  pointer-events: none;
}

.contenido-barra {
  width: 100%;
  z-index: 1;
}
.contenido-barra.fijo {
  position: fixed;
  width: 100%;
  background-color: #14213d;
  opacity: 0.92;
  left: 0;
}

.barra {
  display: flex;
  justify-content: space-between;
  padding: 1.5rem 0;
  align-items: center;
}
.barra a {
  font-weight: 900;
  font-size: 2.5rem;
}
.barra .menu {
  display: block;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .barra .navegacion {
    visibility: visible;
  }
  .barra .navegacion a {
    margin-right: 3rem;
    font-size: 2rem;
    font-weight: 400;
  }
  .barra .navegacion a:last-of-type {
    margin-right: 0;
  }
  .barra .menu {
    display: none;
  }
}

.barra a:hover {
  color: #fca311;
  transition: 0.5s;
}

.navegacion {
  visibility: hidden;
}

@media (max-width: 768px) {
  .navegacion {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 5.2rem;
    right: -100%;
    transition: all 0.6s;
    text-align: center;
    background-color: #14213d;
    opacity: 0.92;
  }
  .navegacion a {
    display: block;
    font-size: 2.5rem;
    margin-left: 1rem;
  }
}
.header {
  height: 100vh;
}
.header .contenido-header {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.titulos {
  margin-top: 13rem;
  align-self: center;
  text-align: center;
}
.titulos h1, .titulos p {
  max-width: 60rem;
}
.titulos button {
  width: 95%;
}

@media (min-width: 768px) {
  .titulos {
    margin-top: 17rem;
  }
  .titulos h1 {
    font-size: 3.8rem;
  }
  .titulos p {
    font-size: 2.5rem;
  }
  .titulos button {
    width: auto;
  }
}
.mostrar {
  visibility: visible;
  right: 0%;
}

.contenedor h2 {
  text-align: center;
}

.contenido-about {
  text-align: start;
}

@media (min-width: 768px) {
  .contenedor h2 {
    display: flex;
    align-items: center;
  }
  .contenedor h2::after {
    content: "";
    background-color: #e5e5e5;
    width: 100%;
    height: 0.2rem;
    margin-left: 1.5rem;
    display: block;
    position: relative;
  }

  .contenido-about {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-skills {
  width: 100%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.contenedor-skills .contenedor-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.contenedor-skills .contenedor-icon img {
  width: 70%;
}

@media (min-width: 768px) {
  .contenedor-skills {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
  .contenedor-skills .contenedor-icon img {
    width: 11rem;
  }
}
.card {
  margin-top: 5rem;
}
.card .descripcion h3, .card .descripcion p {
  text-align: center;
}
.card .descripcion .tecnologias {
  display: flex;
  justify-content: center;
}

.tecnologias {
  margin: 2rem 0;
}
.tecnologias i {
  font-size: 2rem;
  color: #ffffff;
  margin-right: 1rem;
}
.tecnologias i:last-of-type {
  margin-right: 0;
}

@media (min-width: 768px) {
  .card {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .card h3, .card p, .card a {
    margin: 1rem 0 0 0;
  }
  .card.festival .festival-imagen {
    grid-area: 1/2/2/3;
  }
  .card.festival .festival-descripcion {
    grid-area: 1/1/2/2;
  }
  .card.bienes-raices .bienes-imagen {
    grid-area: 1/2/2/3;
  }
  .card.bienes-raices .bienes-descripcion {
    grid-area: 1/1/2/2;
  }
  .card a {
    display: inline-block;
  }
  .card .descripcion .btn-visitar {
    display: flex;
    justify-content: center;
  }
}
.formulario {
  width: 90%;
  margin: 0 auto;
}
.formulario input:not([type=submit]),
.formulario textarea {
  display: block;
  padding: 2rem;
  width: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  border: 1px solid #e5e5e5;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .formulario {
    width: 60rem;
    margin: 0 auto;
  }
  .formulario input:not([type=submit]),
.formulario textarea {
    width: 60rem;
  }
}
.footer {
  text-align: center;
  padding: 3rem 0;
  margin-top: 5rem;
}