.contenido-barra {
    width: 100%;
    z-index: 1;

    &.fijo {
        position: fixed;
        width: 100%;
        background-color: $azulOscuro;
        opacity: 0.92;
        left: 0;
    }
}

.barra {

    display: flex;
    justify-content: space-between;
    padding: 1.5rem 0;
    align-items: center;

    a {
        font-weight:$black;
        font-size: 2.5rem;

    }
    .menu {
        display: block;
        filter: invert(100%);
    }

    @include tablet {
        
        .navegacion {
            visibility: visible;
            a {
                margin-right: 3rem;
                font-size: 2rem;
                font-weight: $regular;
        
                &:last-of-type {
                    margin-right: 0;
                }
            }
    
        }
        .menu {
            display: none;
        }
    }
}
.barra{
    a {
        &:hover {
            color: $amarillo;
            transition: .5s;
        }
    }
}
.navegacion {
    visibility: hidden;

}
@include telefono {
    .navegacion {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 5.2rem;
        right: -100%;
        transition: all .6s;
        text-align: center;
        background-color: $azulOscuro;
        opacity: 0.92;


        
        a {
            display: block;
            font-size: 2.5rem;
            margin-left: 1rem;
 
        }
    }

}
.header {
    height: 100vh;

    .contenido-header {
        height: 100%;
        display: flex;
        flex-direction: column;

    }
}
.titulos {
    margin-top: 13rem;
    align-self: center;
    text-align: center;

    h1, p {
        max-width: 60rem;
    }
    button {
        width: 95%;
    }
}

@include tablet {
    .titulos {
        margin-top: 17rem;

        h1 {
            font-size: 3.8rem;
        }
        p {
            font-size: 2.5rem;
        }
        button {
            width: auto;
        }
        

    }

}

//Menu responsive
.mostrar {
    visibility: visible;
    right: 0%;
}