html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
*,*:before,*:after {
    box-sizing: inherit;
}
body {
    font-family: $fuentePrincipal;
    font-size: 1.6rem;
    line-height: 1.3;
    background-color: $azulOscuro;

    &::-webkit-scrollbar {
        width: 0.7rem;       
    }
    &::-webkit-scrollbar-thumb {
        background-color: darken($gris, 50);
        border-radius: 2rem;
    }
}
p {
    color: $blanco;
    font-size: 1.5rem;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
 
}

a {
    text-decoration: none;
    color: white;
}
img,picture {
    width: 100%;
    display: block;
}
h1, h2, h3 {
    font-family: $fuentePrincipal;
    margin: calc($separacion/2) 0;
    color: $blanco;
}
h1 {
    font-size: 2.5rem;
    font-weight: $black;
}
h2 {
    font-size: 2.3rem;
}
h3 {
    font-size: 2rem;
}
h4 {
    font-size: 1.6rem;
}