.formulario {
    width: 90%;
    margin: 0 auto;

    
    input:not([type = "submit"]),
    textarea {
        display: block;
        padding: 2rem;
        width: 100%;
        margin: 0 auto;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
}
@include tablet{
    
    .formulario {
        width: 60rem;
        margin: 0 auto;
        
        input:not([type = "submit"]),
        textarea {
        width: 60rem;
        
        }
    
    }
    
}
