//Media Query
@mixin telefono {
    @media(max-width: $tablet){
        @content;
    }
}
@mixin tablet {
    @media(min-width: $tablet){
        @content;
    }
}
@mixin desktop {
    @media(min-width: #{desktop}){
        @content;
    }
}
@mixin grid($columnas, $espacio) {
    display: grid;
    gap: $espacio;
    grid-template-columns: repeat($columnas,1fr);
}
@mixin boton ($bgColor, $display){
    background-color: $bgColor;
    color: $blanco;
    font-weight: $bold;
    text-decoration: none;
    padding: 2rem 6rem;
    text-align: center;
    margin-top: calc($separacion/2);
    display: $display;
    border: none;
    border-radius: 1rem;

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }

}
@mixin botonBloqueado ($bgColor, $display){
    background-color: darken($bgColor,50);
    color: $blanco;
    font-weight: $bold;
    text-decoration: none;
    padding: 2rem 6rem;
    text-align: center;
    margin-top: calc($separacion/2);
    display: $display;
    border: none;
    border-radius: 1rem;
    pointer-events: none;

}