.contenedor {
    h2 {
        text-align: center;
    }
}
.contenido-about {
    text-align: start;
}
@include tablet {
    .contenedor {
        h2 {
            display: flex;
            align-items: center;
            &::after {
                content: '';
                background-color: $gris;
                width: 100%;
                height: 0.2rem;
                margin-left: 1.5rem;
                display: block;
                position: relative;
            }
        }
    }
    .contenido-about {
        @include grid(2, 2rem);

    }
    
}

