
.contenedor-skills {
    width: 100%;
    @include grid(2,1rem);

    .contenedor-icon {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;

        img {
            width: 70%;
        }
    }


}

@include tablet {

    .contenedor-skills {
        @include grid(5, 2rem);
        align-items: center;

        .contenedor-icon {

            img {
                width: 11rem;
            }
        }

    }


}



