.card {
    margin-top: 5rem;
    .descripcion {
        h3,p {
            text-align: center;
        }
        .tecnologias {
            display: flex;
            justify-content: center;
        }

 

    }
}
.tecnologias {
    margin: 2rem 0;
    i {
        font-size: 2rem;
        color: $blanco;
        margin-right: 1rem;

        &:last-of-type {
            margin-right: 0;
        }
    
    }
}


@include tablet {
    .card {
        @include grid(2,2rem);
        h3,p,a {
            margin: 1rem 0 0 0;
        }
        &.festival {

            .festival-imagen {
                grid-area: 1/2/2/3;
            }
            .festival-descripcion {
                grid-area: 1/1/2/2;
            }
        }
        &.bienes-raices {
            .bienes-imagen {
                grid-area: 1/2/2/3;
            }
            .bienes-descripcion {
                grid-area: 1/1/2/2;
            }
        }
        a {
            display: inline-block;
        }

        .descripcion {

            .btn-visitar {
                display: flex;
                justify-content: center;
            }
        }

    }
}
